body {
  background: $body-bg;
  color: $text-color;
  font-family: $main-font-secondary;
  font-weight: 300;
  overflow-x: hidden;
}
ul,
li {
  margin: 0;
  padding: 0;
  list-style: none;
}
p {
  font-size: 16px;
}
a,
.btn {
  transition: all 100ms ease-in-out;
  &:hover {
    opacity: 0.6;
  }
}
section {
  width: 100%;
  float: left;
  overflow: hidden;
  min-height: 600px;

  &.section-min {
    min-height: 100px;
  }
  &.section-min-margin {
    min-height: 100px;
    margin-top: $margin-large;
  }
  &.toTop {
    position: relative;
    z-index: 1;
  }
}

a:hover {
  border-color: rgba(0, 0, 0, 0);
}
.tab-pane {
  padding: 0 $padding-base $padding-medium;
}
%section-padding {
  padding: $padding-x-large 0;

  @include smartphone {
    padding: $padding-medium 0;
  }
}

iframe {
  width: 100% !important;
}
a:hover,
a:active,
a:focus {
  outline: none;
}
.btn {
  box-shadow: none !important;
  text-transform: uppercase;

  [class^="ti-"],
  [class*=" ti-"] {
    display: inline-block;
    margin-right: 5px;
  }
  @include smartphone {
    width: 100%;
    margin-right: 0 !important;
    margin-left: 0 !important;
  }
}
button.close,
.close {
  background: $main-light;
  color: $main-theme-color;
  cursor: pointer;
  font-size: 20px !important;
  font-weight: normal;
  opacity: 1;
  padding: $padding-base;
  position: absolute;
  right: 0;
  top: 0;
  z-index: 10;
}
.alert .close {
  background: rgba(0, 0, 0, 0) none repeat scroll 0 0;
  padding: 10px;
  right: -36px;
  top: -13px;
}

.shadow {
  box-shadow: 0 36px 56px 0 rgba(0, 0, 0, 0.05);
}
.overflow-hidden {
  overflow: hidden !important;
}

.form-control {
  border: none;
  box-shadow: none;
  background: none;

  &:focus {
    box-shadow: none;
    outline: 0 none;
  }
}
.white-bg {
  background: white !important;
}
.white-color {
  color: #fff !important;
}

.black-color {
  color: #000 !important;
}

.modal-content {
  float: left;
  margin: 30px 0;
}
.modal-open .modal {
  background: rgba(0, 0, 0, 0.8) none repeat scroll 0 0;
  overflow-x: hidden;
  overflow-y: auto;
}

.modal-backdrop {
  background: rgba(0, 0, 0, 0.9);
}
.no-padding {
  padding: 0;
}

.no-margin {
  margin: 0;
}
.section-heading {
  position: relative;
  margin: $margin-base * 3 0;
  font-size: 90px;
  text-align: center;
  margin-bottom: $margin-large;
  @include smartphone {
    font-size: 60px;
  }
  &:after {
    background: $main-theme-color;
    bottom: -20px;
    content: "";
    height: 4px;
    left: 50%;
    transform: translateX(-50%);
    position: absolute;
    width: 60px;
  }
}
.styled-list {
  list-style: none;

  li {
    padding-left: $padding-base * 1.5;
    position: relative;
    margin-bottom: $margin-base / 2;

    &:before {
      content: "";
      position: absolute;
      left: 0;
      top: 11px;
      width: 15px;
      height: 2px;
      background-color: $main-theme-color;
    }
  }
}
body.cart-widget-open,
body.modal-open {
  overflow-y: hidden !important;
}

.reset-padding {
  padding: 0 !important;
}

.modal,
.card {
  &-content {
    background: $main-dark;
    border: 1px solid rgba($main-light, 0.1);
  }

  &-header {
    border-bottom: 1px solid rgba($main-light, 0.1);
  }
  &-footer {
    border-top: 1px solid rgba($main-light, 0.1);
  }
}
.card {
  border: 1px solid rgba($main-light, 0.1);
  background: $main-dark;
}

