$main-space: 16px;
$font-x-small: 12px;
$font-small: 14px;
$font-default: 16px;

$main-theme-color: #14215a;

$main-theme-color-secondary: #b61841;
$main-dark: #141414;

$main-light: #fff;
$body-bg: #fff;
$nav-link-color: $main-theme-color;

// DROPDOWN
$dropdown-color: $main-light;
$dropdown-link-color: rgba($main-light, 0.7);
$dropdown-bg: $main-dark;
$dropdown-border-radius: 5px;
$dropdown-link-hover-color: $main-light;
$dropdown-link-hover-bg: rgba($main-light, 0.1);
$dropdown-link-disabled-color: rgba($main-light, 0.2);
$dropdown-link-active-color: $main-light;
$dropdown-link-active-bg: $main-theme-color;
$dropdown-border-color: rgba($main-light, 0.1);

// BUTTONS
$primary: $main-theme-color;
$secondary: $main-theme-color-secondary;
$font-sm: 12px;
$font-md: 14px;
$font-lg: 18px;

// P A D D I N G S
$padding-base: 15px;
$padding-medium: 30px;
$padding-large: 60px;
$padding-x-large: 90px;

// M A R G I N S
$margin-base: 15px;
$margin-medium: 30px;
$margin-large: 60px;
$margin-x-large: 90px;

// F O N T S
$h1-font-size: 64px;
$h2-font-size: 48px;
$h3-font-size: 36px;
$h4-font-size: 24px;
$h5-font-size: 18px;
$h6-font-size: 12px;

$main-font: "Montserrat", sans-serif;
$main-font-secondary: "Montserrat", sans-serif;
$main-border-radius: 5px;
$text-color: #141414;

$blue: $main-theme-color;
$blue: #007bff !default;
$indigo: #6610f2 !default;
$purple: #6f42c1 !default;
$pink: #e83e8c !default;
$red: #dc3545 !default;
$orange: #fd7e14 !default;
$yellow: #ffc107 !default;
$green: #28a745 !default;
$teal: #20c997 !default;
$cyan: #17a2b8 !default;

$timeline-number-of-items: 8;
